@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://rsms.me/inter/inter.css');
html {
  font-family: 'Inter', sans-serif;
}
@supports (font-variation-settings: normal) {
  html {
    font-family: 'Inter var', sans-serif;
  }
}

@layer components {
  body {
    @apply font-sans bg-slate-100;
  }

  .btn {
    @apply h-9 py-1 px-2 whitespace-nowrap flex gap-1 font-semibold rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-opacity-75;
  }
  .btn-primary {
    @apply bg-sky-400 text-slate-50 border-2 border-sky-500 hover:bg-sky-500 hover:text-white focus:outline-none focus:ring-2 focus:ring-sky-400/50 btn;
  }
  .btn-secondary {
    @apply bg-slate-200 text-slate-900  border-2 border-slate-300 hover:bg-slate-300 focus:outline-none focus:ring-2 focus:border-sky-500 focus:ring-sky-400/50 btn;
  }

  .btn-disabled {
    @apply bg-slate-300 text-slate-500  border-2 border-slate-300 hover:bg-slate-300 focus:outline-none focus:ring-0;
  }

  .btn-icon {
    @apply p-1 text-slate-900 hover:bg-slate-300/30 focus:outline-none focus:ring-2 focus:ring-slate-300 rounded-md flex justify-center items-center;
  }
  /* 
  a {
    @apply hover:bg-slate-300 focus:outline-none focus:ring-2 focus:border-sky-500 focus:ring-sky-400/50 btn;
  } */

  .bv-form-item-input {
    @apply h-9 bg-slate-50 border-2 rounded-md border-slate-300 focus:shadow-none focus:outline-none focus:ring focus:ring-sky-400/50 focus:border-sky-500;
  }

  .bv-form-item-radio-indicator {
    @apply flex items-center justify-center h-full w-full relative after:block after:w-3 after:h-3 after:bg-black after:rounded-full;
  }

  .bv-dow-picker [data-state='on'] {
    @apply bg-sky-500 text-slate-50 focus:bg-sky-500 hover:text-slate-50 hover:bg-sky-600;
  }
}
