@import "../../../../../../00_common/01_scss/variables";

.main {
  //border: 1px solid blue;

  display: grid;
  grid-template-rows: 1fr 1fr;
  height: $tableHeadHeight;

  font-size: 14px;
  font-weight: 600;

  .month {
    //border: 1px solid red;
    padding-left: 4px;
    font-size: 20px;
    color: $slate-300;
    margin-left: -2px;
    //&.borderLeft {
    //  border-left: 1px solid $borderColor;
    //}
    &.borderCell {
      border-left: 2px solid $slate-200;
    }
  }

  .dayWeek {
    //border: 1px solid green;

    //border-left: 1px solid $borderColor;
    border-right: 2px solid $slate-200;

    .boundary {
      border-right: 2px solid $slate-300;
    }

    .day,
    .week {
      background: $slate-50;
      padding-left: 4px;
      padding-right: 4px;
    }

    .day {
      font-weight: normal;
    }
  }
}
